import "./styles.css";
import React from "react";
import Card from "@mui/material/Card";
import CardHeader from "@mui/material/CardHeader";
import Avatar from "@mui/material/Avatar";
import Typography from "@mui/material/Typography";
import CardContent from "@mui/material/CardContent";
import Stack from "@mui/material/Stack";
import Divider from "@mui/material/Divider";
import IconButton from "@mui/material/IconButton";
import DeleteIcon from "@mui/icons-material/Delete";
import ModeEditIcon from "@mui/icons-material/ModeEdit";
import { deepPurple } from "@mui/material/colors";

export default function Card_Display(props) {
  const handleEdit = (e) => {
    props.changeEdit(props.detail.formvalues.id);
  };

  const handleDelete = (e) => {
    const del = document.querySelector(".feedBack");
    del.removeChild(document.getElementById(props.detail.formvalues.id));
  };

  return (
    <Card
      id={props.detail.formvalues.id}
      sx={{ maxWidth: 450, margin: "0px 0 30px 380px", padding: 3 }}
    >
      <CardHeader
        avatar={
          <Avatar
            aria-label="profile"
            src="data:image/jpeg;base64,"
          />
        }
        action={
          <Stack direction="row" spacing={2}>
            <IconButton
              aria-label="edit"
              size="small"
              style={{ color: deepPurple[500] }}
            >
              <ModeEditIcon fontSize="small" onClick={handleEdit} />
            </IconButton>
            <IconButton
              aria-label="delete"
              size="small"
              style={{ color: deepPurple[500] }}
            >
              <DeleteIcon fontSize="small" onClick={handleDelete} />
            </IconButton>
          </Stack>
        }
        title={props.detail.formvalues.name}
        subheader={props.detail.formvalues.email}
      ></CardHeader>
      <Divider />
      <CardContent>
        <Typography variant="body2" color="text.secondary">
          Gender : {props.detail.formvalues.gender}
        </Typography>
        <Typography variant="body2" color="text.secondary">
          Feedback : {props.detail.formvalues.feedback}
        </Typography>
      </CardContent>
    </Card>
  );
}
